@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css?family=Federant&display=swap);
.searchRow {
  margin-top: 4rem;
}

div.search {
  margin-bottom: 1.5rem;
}

input#searchInput {
  margin-bottom: 0px;
}
.stockTrend {
  margin-left: 1.25rem;
}
li.stockListItem:nth-child(even) div.collapsible-header {
  background-color: rgb(245, 245, 245);
}

span.stockSymbol {
  font-weight: 600;
  margin-top: 7px;
}

span.stockPrice {
  margin-left: auto;
}

p.chartDisclaimer {
  font-size: smaller;
  color: rgb(75, 75, 75)
}

li.active {
  border: 2px solid #006064;
}
i.material-icons.menu-icon {
  margin-left: 2rem;
  font-size: 1.1rem;
}

li.collection-item:nth-child(even) {
  background-color: lightgray;
}

h4.noStocks {
  padding: 2rem;
}
body {
  font-family: 'Abel';
}

span.brand-logo {
  font-family: 'Federant';
}
